import Vue from 'vue'
import Router from 'vue-router'
import { Home, AboutUs } from '@/components'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about-us',
      name: 'about_us',
      component: AboutUs
    }
  ]
})



